.about {
    margin-bottom: 84px
}

.about__title {
    @include font (600, 24px, 3.33);
    color: $color-title-primary;
    text-align: center;
    margin-bottom: 24px;
}

.about__img {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 23px;
}

.about__text {
    @include font (none, 13px, 1.54);
    text-align: center;
}

.about__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.about__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding-top: 60px;
}

.about__number {
    position: absolute;
    top: 0;
    @include font (600, 24px, 3.33);
    color: $color-text-secondary;
    text-align: center;
}